<template>
	<div class="message" :class="{ '_showed': showSystemMessage }">
		<div class="message__content" :class="{ '_error': isSystemMessageError }">
			{{ systemMessage }}
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';

	export default {
		name: "Message",

		data() {
			return {
				timeout: null,
				showSystemMessage: false,
			}
		},

		methods: {
			startMessageDisplaying() {
				this.showSystemMessage = true;

				if (null !== this.timeout) {
					clearTimeout(this.timeout);
					this.timeout = null;
				}

				this.timeout = setTimeout(() => {
					this.timeout = null;
					this.showSystemMessage = false;

					setTimeout(() => app.hideMessage(), 500);

				}, this.systemMessageTimeout);
			}
		},

		computed: {
			...mapGetters([
				'systemMessage',
				'systemMessageTimeout',
				'isSystemMessageError',
			])
		},

		watch: {
			systemMessage(massage) {
				massage && this.startMessageDisplaying();
			}
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.message {
		position: fixed;
		display: flex;
		align-items: center;
		justify-content: center;
		bottom: 2rem;
		left: -2rem;
		width: 280px;
		border-radius: 4px;
		background-color: @color-dark;
		z-index: 1;
		transition: all 0.3s ease-in-out;
		opacity: 0;

		&._showed {
			left: 2rem;
			opacity: 1;
		}

		&__content {
			padding: 1rem;
			color: @color-gold;
			text-align: center;

			&._error {
				color: @color-pink;
			}
		}
	}
</style>


